@import "~theme.scss";


.root {
  @extend .container;
  @include spacing(padding-top, 160);
  @include spacing(padding-bottom, 160);
  @include font-sizing(40);
  width: 100%;
  font-weight: 600;
  letter-spacing: 1.6px;
  color: #7f7592;
  em {
    font-style: normal;
    color: white;
  }
  text-align: center;
  @media (max-width: $breakpoint-mobile)  {
    letter-spacing: 0.8px;
  }
}