@import '~theme.scss';

.root {
  @extend .container;
  .content {
    display: grid;
    grid-template-columns: repeat(3, 320px);
    @media (max-width: $breakpoint-middle) {
      grid-template-columns: repeat(2, 320px);
    }
    @media (max-width: $breakpoint-mobile) {
      grid-template-columns: repeat(1, 320px);
    }

    column-gap: 20px;
    @include spacing(row-gap, 56);
    img {
      height: 100%;
      width: 100%;
    }
  }
  @include spacing(margin-bottom,160);
}

.title {
  display: none;
  font-size: 32px;
  font-weight: bold;
  text-align: center;
  color: #ffffff;
  @include spacing(margin-bottom, 56);
  @media (max-width: $breakpoint-middle) {
    display: block;
  }
}

@media (max-width: $breakpoint-middle) {
  .cover {
    display: none;
  }
}

.profileCard {
  text-align: center;
  .role {
    white-space: break-spaces;
    font-family: Roboto;
    @include font-sizing(20);
    color: #7f7592;
    letter-spacing: 0.4px;
  }
  .name {
    font-family: Roboto;
    @include font-sizing(20);
    font-weight: bold;
    letter-spacing: 0.4px;
    color: #ffffff;
    margin-bottom: 8px;
    margin-top: 16px;
  }
}

.photoGroup {
  position: relative;
  border-radius: 8px;
  overflow: hidden;
  width: 320px;
  height: 320px;
  .overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    visibility: hidden;
    background-color: rgba(0,0,0,0.5);
    z-index: 5;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    a {
      color: gray;
      &:hover {
        color: white;
      }
      font-size: 48px;
    }
  }
  &:hover .overlay{
    visibility: visible;
  }
}

.cover {
  height: 320px;
  width: 320px;
  border-radius: 8px;
  border: solid 2px #7f7592;
  background-color: #1c1823;
  @include spacing(padding, 32);
  box-sizing: border-box;
  > div:nth-child(1) {
    color: #ffffff;
    font-weight: bold;
    @include font-sizing(48);
  }
}