@import "~theme.scss";

.root {
  height: calc(100vh - 72px);
  @media (max-width: $breakpoint-middle) {
    height: calc(100vh - 68px);
  }
  @media (max-width: $breakpoint-mobile) {
    height: calc(100vh - 52px);
  }
  width: 100%;
  position: relative;
  .background {
    position: absolute;
    height: calc(100vh - 72px);
    @media (max-width: $breakpoint-middle) {
      height: calc(100vh - 68px);
    }
    @media (max-width: $breakpoint-mobile) {
      height: calc(100vh - 52px);
    }
    @extend .container;
    width: 100%;
    top: 0;
    left: 0;
    right: 0;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-direction: column;
    z-index: -1;
    overflow: hidden;
    @media (max-width :$breakpoint-mobile) {
      padding: 0;
    }
    > img {
      height: 64vh;
      position: relative;
      left: calc(800px - 25vh);
      opacity: 0.5;
      @media (max-width :$breakpoint-middle) {
        height: 80vh;
        left: calc(410px - 25vh);
      }
      @media (max-width :$breakpoint-mobile) {
        right: -7%;
        left: auto;
        width: 100%;
        height: auto;
        top: -17vh;
      }
    }
  }
  .content {
    @extend .container;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: self-start;
    @media (max-width: $breakpoint-middle) {
      align-items: flex-start;
      text-align: center;
    }
    @media (max-width: $breakpoint-mobile) {
      align-items: center;
    }

    justify-content: center;
    h1, h2 {
      font-weight: 600;
      @include font-sizing(96);
      text-shadow: 0 4px 40px rgba(0, 0, 0, 0.48);
      margin: 0;
      letter-spacing: 3.84px;
      color: white;
      color: #ffffff;
      @media (max-width: $breakpoint-mobile) {
        letter-spacing: 1.28px;
      }
    }
    > h2 {
      @include spacing(margin-bottom, 24);
    }
    .description {
      @include font-sizing(20);
      color: #cbc4d9;
      font-family: 'Roboto';
      @include spacing(margin-bottom, 56);
    }
    .buttons {
      display: flex;

      > div {
        padding: 16px 24px;
        @include font-sizing(20);
        font-family: 'Roboto';
        font-weight: 500;

        &:hover {
          color: white !important;
          border: none !important;
          background-image: linear-gradient(to right, #ffbe99, #9d50ff, #6109d1);
          cursor: pointer !important;
        }
      }

      > div:nth-child(1) {
        border-radius: 8px;
        background-color: #cbc4d9;
        color: black;
        @include spacing(margin-right, 24);
      }

      > div:nth-child(2) {
        border-radius: 8px;
        border: solid 1px rgba(181, 174, 189, 0.64);
        background-color: rgba(255, 255, 255, 0.08);
        color: white;
        margin: -1px;
        &:hover {
          margin: 0 !important;
        }
      }

      @media (max-width: $breakpoint-mobile) {
        flex-direction: column;
        > div:nth-child(1) {
          margin-right: 0;
          margin-bottom: 16px;
        }
      }
    }
  }
  @media (max-width: $breakpoint-mobile) {
    display: block;
  }
}
:global(.observed).background {
  animation-delay: 0.3s;
}
:global(.observed).content {
  animation-delay: 0.5s;
}
