@import "~theme.scss";

.root {
  height: calc(100vh - 72px);
  @media (max-width: $breakpoint-middle) {
    height: calc(100vh - 68px);
  }
  @media (max-width: $breakpoint-mobile) {
    height: calc(100vh - 52px);
  }
  @extend .container;
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  justify-content: center;
  .content {
    @include font-sizing(48);
    font-weight: bold;
    background: linear-gradient(95.4deg, #9FDCDA 21.01%, #4251F5 46.93%, #CC4DE0 67.5%, #F57953 100%);
    background-clip: text;
    color: transparent;
    line-height: 1.33;
    letter-spacing: 1.92px;
    .noGradation {
      color: white;
    }
  }
  @include spacing(margin-bottom, 160);
}