@import "~theme.scss";

.root {
  @extend .container;
  text-align: center;
  > .columns {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  > .tag {
    @include spacing(margin-bottom, 16);
    @include font-sizing(16);
    color: #7f7592;
    font-weight: bold;
  }

  > .title {
    @include font-sizing(40);
    @include spacing(margin-bottom, 56);
    color: #ffffff;
    font-weight: bold;
  }
  @include spacing(margin-bottom, 160);
}

.column {
  width: 320px;
  text-align: left;
  @media (max-width: $breakpoint-middle)  {
    width: 45%;
    animation-delay: 0s !important;
    &:last-child {
      margin-top: 56px !important;
    }
  }
  @media (max-width: $breakpoint-mobile)  {
    width: 100%;
    &.column + &.column {
      margin-top: 48px !important;
    }
  }
  img {
    width: 100%;
    @include spacing(margin-bottom, 32);
  }
  > .title {
    font-family: Roboto;
    @include font-sizing(20);
    @include spacing(margin-bottom, 32);
    font-weight: bold;
    line-height: 1.4;
    text-align: left;
    color: #ffffff;
  }
  > .description {
    font-family: Roboto;
    @include font-sizing(20);
    color: rgba(255, 255, 255, 0.8);
    line-height: 1.2;
    @include spacing(margin-bottom, 32);
  }
  > .items {
    > div {
      font-family: Roboto;
      @include font-sizing(20);
      line-height: 1.2;
      color: #ffffff;
      display: flex;
      align-items: center;
      > * + * {
        @include spacing(margin-left, 16);
      }
    }
    > div + div {
      @include spacing(margin-top, 16);
    }
  }
}

