@import "~theme.scss";


:export {
  breakpointMobile: $breakpoint-mobile;
  breakpointMiddle: $breakpoint-middle;
}


@keyframes unfoldIn {
  0% {
    transform: scaleX(0) scaleY(0.005);
  }
  50% {
    transform: scaleX(1) scaleY(0.005);
  }
  100% {
    transform: scaleY(1) scaleX(1);
  }
}


.root {
  @extend .container;
  text-align: center;
  .tag {
    font-weight: 600;
    color: #7f7592;
    letter-spacing: 1.28px;
    @include font-sizing(16);
  }
  .title {
    @include spacing(margin-top, 16);
    @include spacing(margin-bottom, 56);
    @include font-sizing(48);
    color: #ffffff;
    font-weight: 600;
  }
  .cards {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    @media (max-width: $breakpoint-mobile) {
      justify-content: center;
      flex-direction: column;
      align-items: center;
    }
    .demoCard {
      box-sizing: border-box;
      width: calc(33% - 16px);
      @media (max-width: $breakpoint-middle) {
        width: calc(50% - 10px);
      }
      @media (max-width: $breakpoint-mobile) {
        width: 320px;
      }
    }
    @include spacing(margin-bottom, 56);
  }
}



.demoCard:last-child {
  @media (max-width: $breakpoint-middle) {
    margin-top: 56px;
  }
}
.demoCard + .demoCard {
  @media (max-width: $breakpoint-mobile) {
    margin-top: 48px;
  }
}


.demoCard {
  border-radius: 8px;
  background-color: #1c1823;
  display: flex;
  flex-direction: column;
  align-items: center;
  @include spacing(padding-left, 32);
  @include spacing(padding-right, 32);
  @include spacing(padding-top, 32);
  @include spacing(padding-bottom, 32);
  .cardID {
    width: 100%;
    text-align: left;
    font-family: Roboto;
    @include font-sizing(20);
    font-weight: 300;
    line-height: 1.4;
    color: #bcacd1;
  }
  .cardBody {
    color: #958ca5;
    font-family: Roboto;
    @include font-sizing(20);
    line-height: 1.4;
    em {
      font-style: normal;
      color: white;
      font-weight: bold;
    }
    text-align: left;
  }
  img {
    @include spacing(margin-top, 16);
    @include spacing(margin-bottom, 24);
  }
}

.browser {
  border-radius: 8px;
  overflow: hidden;
  border: solid 1px rgba(255, 255, 255, 0.04);
  background-color: #1c1823;
  .browserHeader {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
    box-shadow: 0 1px 1px 0 #17141d;
    background-color: #292432;
    .url {
      font-size: 16px;
      font-weight: 600;
      line-height: 1.5;
      color: #7f7592;
    }
    .buttons {
      display: flex;
      position: absolute;
      left: 20px;
      top: 14px;
      > div {
        width: 12px;
        height: 12px;
        border-radius: 6px;
      }
      > div:nth-child(1) {
        background-color: #fe5951;
      }
      > div:nth-child(2) {
        background-color: #feb638;
      }
      > div:nth-child(3) {
        background-color: #25c13f;
      }
      > div + div {
        margin-left: 8px;
      }
    }
  }
  .browserBody {
    overflow-y: auto;
    overflow-x: hidden;
    pre {
      margin: 0 !important;
      overflow-x: hidden !important;
    }
  }
}

.demoPreview {
  display: flex;
  justify-content: space-between;
  @media (max-width: $breakpoint-middle) {
    flex-wrap: wrap;
    justify-content: center;
    flex-direction: column;
    align-items: center;
  }
  .preview {
    width: calc(50% - 10px);
    height: 320px;
    @media (max-width: $breakpoint-middle) {
      width: 490px;
      margin-top: 40px;
    }
    @media (max-width: $breakpoint-mobile) {
      width: 320px;
    }
  }
  .browsers {
    width: calc(50% - 10px);
    position: relative;
    @media (max-width: $breakpoint-middle) {
      width: 490px;
    }
    @media (max-width: $breakpoint-mobile) {
      width: 320px;
    }
    .runButton {
      position: absolute;
      right: 16px;
      bottom: 16px;
      width: 68px;
      height: 48px;
      border-radius: 8px;
      background-color: #6642f5;
      font-family: Roboto;
      @include font-sizing(20);
      font-weight: 600;
      line-height: 1.2;
      color: rgba(255, 255, 255, 0.9);
      display: flex;
      justify-content: center;
      align-items: center;
      &:hover {
        cursor: pointer;
        background-color: #7f5cff;
      }
    }
  }
}

.request {
  width: 100%;
  .browserBody {
    height: 280px;
  }
}

.response {
  position: absolute;
  bottom: -32px;
  left: 32px;
  right: 32px;
  height: 246px;
  animation: unfoldIn 0.2s cubic-bezier(0.165, 0.84, 0.44, 1) forwards;
  .browserHeader {
    height: 32px;
  }
  .browserBody {
    height: calc(100%  - 32px);
  }

  .buttons {
    > div:hover {
      cursor: pointer;
      background-color: #ff9797 !important;
    }
  }
}

.previewTab {
  border-radius: 8px;
  background-color: #272130;
  color: #7f7592;
  display: inline-flex;
  font-weight: 600;
  position: relative;
  @include font-sizing(16);
  > div {
    padding-top: 10px;
    padding-bottom: 10px;
    z-index: 1;
    min-width: 120px;
    @media (max-width: $breakpoint-mobile) {
      min-width: 0px;
    }
    @include spacing(padding-left, 24);
    @include spacing(padding-right, 24);
    box-sizing: border-box;
    text-align: center;
    &:hover {
      cursor: pointer;
      color: #ffffff;
    }
  }

  > div.selectedTab {
    color: #ffffff;
    &:hover {
      cursor: default;
    }
  }
  @media (max-width: $breakpoint-middle) {
    display: flex;
    width: 100%;
    > div {
      width: 100%;
    }
  }
}
.previewHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  >div:nth-child(1) {
    @include font-sizing(20);
    line-height: 1.2;
    color: #ffffff;
    font-weight: bold;
    align-items: center;
    em {
      font-style: normal;
      color: #5ab9ee;
      margin-left: 8px;
    }
  }
  @include spacing(margin-bottom, 16);
  @media (max-width: $breakpoint-middle) {
    flex-direction: column-reverse;
    margin-bottom: 0;
  }
  >div:nth-child(1) {
    @media (max-width: $breakpoint-middle) {
      @include spacing(padding-top, 56);
      @include spacing(padding-bottom, 16);
    }
  }
}

.contactUsCard {
  background-image: linear-gradient(to right, #f5d5e4, #9d50ff);
  width: 100%;
  box-sizing: border-box;
  position: relative;
  overflow: hidden;
  text-align: left;
  @media (max-width: $breakpoint-mobile) {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding-left: 16px !important;
    padding-right: 16px !important;
  }
  img {
    position: absolute;
    top: 0;
    height: 100%;
    right: 0;
    @media (max-width: $breakpoint-middle) {
      right: -150px;
    }
    @media (max-width: $breakpoint-mobile) {
      position: static;
      width: 100%;
      margin-top: 0;
    }
    z-index: 0;
  }
  justify-content: space-between;
  @include spacing(padding-top, 24);
  @include spacing(padding-bottom, 24);
  @include spacing(padding-left, 32);
  @include spacing(margin-top, 160);
  @include spacing(margin-bottom, 160);
  border-radius: 8px;

  > div {
    > div:nth-child(1) {
      color: #000000;
      font-weight: bold;
      @include font-sizing(48);
      @include spacing(margin-bottom, 16);
    }
    > div:nth-child(2) {
      color: #000000;
      font-weight: 500;
      line-height: 1.4;
      font-family: Roboto;
      @include font-sizing(20);
      @include spacing(margin-bottom, 32);
    }
    > div:nth-child(3) {
      color: #ffffff;
      font-weight: 500;
      font-family: Roboto;
      @include font-sizing(20);
      padding: 16px 24px;
      border-radius: 8px;
      background-color: #141119;
      display:inline-block;
      &:hover {
        cursor: pointer;
        background-color: #6d17da;
        color: white;
      }
    }
  }
  @media (max-width: $breakpoint-mobile) {
    padding-bottom: 0;
  }
}

.selection {
  position: absolute;
  top: 0;
  bottom: 0;
  background-color: #6642f5;
  border-radius: 8px;
  padding: 0 !important;
  &:hover {
    cursor: none !important;
  }
  transition: all 0.1s ease;
  z-index: 0 !important;
}

.searchDefaultPreview {
  width: 100%;
  height: 100%;
  overflow-y: auto;
  border-radius: 8px;
  background-color: #1c1823;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;

  > img {
    width: calc(100% - 32px);
    margin-top: 16px;
    box-sizing: content-box;
    @media (max-width: $breakpoint-middle)  {
      height: auto;
    }
  }
}
.searchActivePreview {
  width: 100%;
  height: 100%;
  border-radius: 8px;
  overflow: hidden;
  position: relative;
  z-index: 15;
  > div {
    position: relative;
    z-index: 10;
    video {
      object-fit: cover;
      z-index: 10;
    }
  }
  .overlay {
    z-index: 20;
    top: 16px;
    left: 0px;
    right: 0px;
    bottom: 0px;
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    > div {
      padding: 8px;
      font-family: Roboto;
      font-weight: 500;
      @include font-sizing(16);
      display: inline-flex;
      background-color: rgba(0, 0, 0, 0.48);
      border-radius: 8px;
      > span:nth-child(1) {
        color:white;
      }
      > span:nth-child(2) {
        margin-left: 12px;
        color:#a661ff;
      }
    }
    img {
      width: 100%;
    }
  }
}

.captionDefaultPreview {
  width: 100%;
  height: 100%;
  border-radius: 8px;
  overflow: hidden;
  position: relative;
  z-index: 15;
  > div {
    position: relative;
    z-index: 10;
    video {
      object-fit: cover;
    }
  }
  .overlay {
    z-index: 20;
    top: 16px;
    left: 16px;
    right: 16px;
    bottom: 16px;
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    > div {
      padding: 8px;
      font-family: Roboto;
      font-weight: 500;
      @include font-sizing(16);
      display: inline-flex;
      background-color: rgba(0, 0, 0, 0.48);
      border-radius: 8px;
      &:first-child > span:nth-child(1) {
        color:white;
      }
      &:first-child > span:nth-child(2) {
        margin-left: 12px;
        color:#a661ff;
      }

      &:nth-child(2) {
        color:white;
        width: 100%;
        justify-content: flex-start;
        align-items: center;
        font-family: Roboto;
        @include font-sizing(20);
        >span {
          margin-left: 8px;
        }
      }


    }
  }
}
.captionActivePreview {
  width: 100%;
  height: 100%;
  position: relative;
  z-index: 15;
  > div {
    border-radius: 8px;
    overflow: hidden;
    position: relative;
    z-index: 10;
    video {
      object-fit: cover;
    }
  }
  .overlay {
    z-index: 20;
    top: 16px;
    bottom: -32px;
    left: 32px;
    right: 32px;
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    > .browser {
      height: 246px;
      animation: unfoldIn 0.2s cubic-bezier(0.165, 0.84, 0.44, 1) forwards;
      .browserHeader {
        height: 32px;
      }
      .browserBody {
        font-family: Roboto;
        @include font-sizing(20);
        line-height: 1.6;
        color: rgba(255, 255, 255, 0.8);
        padding: 16px;
        text-align: left;
      }
    }
    > div:first-child {
      padding: 8px;
      font-family: Roboto;
      font-weight: 500;
      @include font-sizing(16);
      display: inline-flex;
      background-color: rgba(0, 0, 0, 0.48);
      border-radius: 8px;
      > span:nth-child(1) {
        color:white;
      }
      > span:nth-child(2) {
        margin-left: 12px;
        color:#a661ff;
      }
    }
  }
}

.browser {
  pre {
    background-color: transparent !important;
    text-align: left;
    padding: 16px;
    span {
      color: white;
      @include font-sizing(16);
    }

    :global(.token):global(.string) {
      color: #9bc9ff;
    }
    :global(.token):global(.method) {
      color: #ff58ee;
    }
    :global(.token):global(.comment) {
     color: rgba(255,255,255,0.5);
    }
    :global(.token):global(.parameter) {
     color: rgb(255, 181, 110);
    }
    :global(.token):global(.boolean) {
     color: rgb(39, 231, 255);
    }
    :global(.token):global(.number) {
     color: rgb(255, 243, 78);
    }
  }
}

@media (max-width: $breakpoint-mobile) {
  .toDelete {
    display: none;
  }
}

.overlay .browser .buttons > div:hover {
  cursor: pointer;
  background-color: #ff9797 !important;
}

@media (max-width: $breakpoint-middle) {
  .demoCard {
    animation-delay: 0s !important;
  }
}