@import "~theme.scss";

.root {
  @extend .container;
  @include spacing(margin-bottom, 160);
  display: flex;
  justify-content: space-between;
  > div {
    width: 50%;
  }

  > div > .tag {
    @include spacing(margin-bottom, 16);
    @include font-sizing(16);
    color: #7f7592;
    font-weight: bold;
  }
  > div > .title {
    @include font-sizing(40);
    color: #ffffff;
    font-weight: bold;
  }
  @media (max-width: $breakpoint-middle) {
    flex-direction: column;
    > div {
      width: 100%;
    }
    > div > .title {
      @include spacing(margin-bottom, 56);
    }
  }
  @media (max-width: $breakpoint-mobile) {
    text-align: center;
  }
}

.reason {
  display: flex;
  justify-content: flex-start;
  @include spacing(padding, 32);
  border-radius: 8px;
  background-color: #1c1823;
  align-items: center;
  img {
    width: 150px;
    height: auto;
    margin-top: auto;
    margin-bottom: auto;
    @include spacing(margin-right, 32);
  }
  .text {
    width: auto;
    > div + div {
      @include spacing(margin-top, 8);
    }
    .title {
      font-family: Roboto;
      @include font-sizing(20);
      font-weight: bold;
      color: #ffffff;
    }
    .description {
      font-family: Roboto;
      @include font-sizing(20);
      line-height: 1.4;
      font-weight: normal;
      color: #7f7592;
    }
  }
  @media (max-width: $breakpoint-mobile) {
    flex-direction: column;
    img {
      margin-right: 0;
      margin-bottom: 24px;
    }
  }
}

.reason + .reason {
  @include spacing(margin-top, 56);
}

.reasons {
  animation: none;
  opacity: 1;
}

@media (min-width: calc($breakpoint-middle + 1px)) {
  .reason:global(.observed) .reason {
    animation: appear $animation-duration;
  }
}

@media (max-width: $breakpoint-middle) {
  .reason {
    animation-delay: 0s !important;
  }
}