$breakpoint-xs: 380px;
$breakpoint-mobile: 768px;
$breakpoint-middle: 1064px;
$animation-duration: 1.2s;

.container {
  width: 100%;
  padding-left: calc(50% - 512px);
  padding-right: calc(50% - 512px);
  box-sizing: border-box;
  @media (max-width: $breakpoint-middle) {
    padding-left: calc(50% - 330px);
    padding-right: calc(50% - 330px);
  }
  @media (max-width: $breakpoint-mobile) {
    padding-left: calc(50% - 160px);
    padding-right: calc(50% - 160px);
  }
}

$font-data : (
  'default' : (
    16: 16px,
    20: 20px,
    32: 32px,
    40: 40px,
    48: 48px,
    96: 80px
  ),
  'middle' : (
    16: 16px,
    20: 20px,
    32: 32px,
    40: 32px,
    48: 32px,
    96: 56px
  ),
  'mobile' : (
    16: 14px,
    20: 16px,
    32: 24px,
    40: 24px,
    48: 24px,
    96: 32px
  )
);

$spacing-data: (
  'default': (
    8: 8px,
    16: 16px,
    24: 24px,
    32: 32px,
    56: 56px,
    160: 160px,
  ),
  'middle': (
    8: 8px,
    16: 16px,
    24: 24px,
    32: 32px,
    56: 56px,
    160: 160px,
  ),
  'mobile': (
    8: 8px,
    16: 8px,
    24: 16px,
    32: 24px,
    56: 48px,
    160: 120px,
  ),
);

@mixin spacing($property, $value) {
  #{$property}: map-get(map-get($spacing-data, 'default'), $value);
  @media (max-width: $breakpoint-middle) {
    #{$property}: map-get(map-get($spacing-data, 'middle'), $value);
  }
  @media (max-width: $breakpoint-mobile) {
    #{$property}: map-get(map-get($spacing-data, 'mobile'), $value);
  }
}

@mixin font-sizing($value) {
  font-size: map-get(map-get($font-data, 'default'), $value);
  @media (max-width: $breakpoint-middle) {
    font-size: map-get(map-get($font-data, 'middle'), $value);
  }
  @media (max-width: $breakpoint-mobile) {
    font-size: map-get(map-get($font-data, 'mobile'), $value);
  }
}

.tag {
  letter-spacing: 0.96px;
}

@keyframes appear {
  0% {
    transform: translateY(80px);
    opacity: 0;
  }
  100% {
    transform: translateY(0px);
    opacity: 1;
  }
}

:global(.observing) {
  opacity: 0;
  animation-fill-mode:forwards !important;
}
:global(.observed) {
  animation: appear $animation-duration;
  animation-delay: 0s;
}

br.mobile {
  display: none;
}
br.middle {
  display: none;
}
@media (max-width: $breakpoint-middle) {
  br.mobile {
    display: none;
  }
  br.desktop {
    display: none;
  }
  br.middle {
    display: inline;
  }
}
@media (max-width: $breakpoint-mobile) {
  br.mobile {
    display: inline;
  }
  br.desktop {
    display: none;
  }
  br.middle {
    display: none;
  }
}