@import "~theme.scss";

.root {
  text-align: center;
  position: relative;
  @include spacing(padding-top, 56);
  @include spacing(margin-bottom, 160);
  > .tag {
    @include spacing(margin-bottom, 16);
    @include font-sizing(16);
    color: #8567ff;
    font-weight: bold;
  }

  > .title {
    @include font-sizing(40);
    @include spacing(margin-bottom, 56);
    color: #ffffff;
    font-weight: bold;
    line-height: 1.2;
    letter-spacing: 0.96px;
  }
  > .waitlistBtn {
    padding: 16px 24px;
    @include font-sizing(20);
    font-family: 'Roboto';
    font-weight: 500;
    border-radius: 8px;
    background-color: #cbc4d9;
    color: black;
    @include spacing(margin-right, 24);
    display: inline-block;

    &:hover {
      color: white !important;
      border: none !important;
      background-image: linear-gradient(to right, #ffbe99, #9d50ff, #6109d1);
      cursor: pointer !important;
    }
  }
}

.beta {
  width: 136px;
  display: inline-block;
  position: relative;
  @media (max-width: $breakpoint-middle) {
    width: 98px;
  }
  @media (max-width: $breakpoint-mobile) {
    width: 68px;
  }
  >img {
    position: absolute;
    width: 100%;
    left: 0;
    bottom: -4px;
  }
}

.bg {
  position: absolute;
  width: 100%;
  height: 100vh;
  top: calc(50% - 50vh);
  background-image: radial-gradient(rgba(157, 80, 255, 0.16) 0, rgba(157, 80, 255, 0) 640px );
  @media (max-width: $breakpoint-middle) {
    background-image: radial-gradient(rgba(157, 80, 255, 0.16) 0, rgba(157, 80, 255, 0) 440px );
  }
  @media (max-width: $breakpoint-mobile) {
    background-image: radial-gradient(rgba(157, 80, 255, 0.16) 0, rgba(157, 80, 255, 0) 280px );
  }

}