@import "~theme.scss";

.root {
  @extend .container;
  display: flex;
  @include spacing(padding-top, 24);
  @include spacing(padding-bottom, 24);
  justify-content: space-between;
  align-items: center;
  position: sticky;
  top: 0;
  background-color: #1c1823;
  img {
    height: 20px;
    @media (max-width: $breakpoint-middle) {
      height: 16px;
    }
  }
  z-index: 100;
  div:last-child a {
    color: #7f7592;
    @include font-sizing(20);
    font-weight: bold;
    &:hover {
      color: white;
    }
    &.selected {
      color: white;
      @media (max-width: $breakpoint-mobile) {
        display: none;
      }
    }
    &:last-child {
      @media (max-width: $breakpoint-mobile) {
        display: none;
      }
    }
    text-decoration: none;
  }
  a + a {
    @include spacing(margin-left, 32);
  }
}
