@import '~theme.scss';

@media (max-width: $breakpoint-mobile) {
  .title, .tag {
    text-align: center;
  }
}

.root {
  @extend .container;
  @include spacing(margin-bottom, 160);
}

.tag {
  @include font-sizing(16);
  font-weight: bold;
  letter-spacing: 1.28px;
  color: #7f7592;
  @include spacing(margin-bottom, 16);
}

.title {
  @include font-sizing(48);
  font-weight: bold;
  color: #ffffff;
  @include spacing(margin-bottom, 56);
}

@media (max-width: $breakpoint-middle) {
  .tag, .title {
    text-align: center;
  }
}

.articles {
  display: flex;
  justify-content: space-between;
  @media (max-width: $breakpoint-middle) {
    flex-direction: column;
    align-items: center;
    > div + div {
      @include spacing(margin-top, 56);
    }
  }
}

.anchorBG {
  padding: 3px;
  &:hover {
    background-image: linear-gradient(to right, #ffbe99, #9d50ff, #6109d1);
    border-radius: 8px;
  }
}

.articleAnchor {
  position: relative;
  width: 490px;
  text-decoration: none;
  border-radius: 8px;
  background-color: #1c1823;
  overflow: hidden;
  display: block;
  @media (max-width: $breakpoint-mobile) {
    width: 320px;
  }

  > img {
    width: 100%;
  }
  .foreground {
    @include spacing(padding, 32);
    .text {
      @include spacing(margin-top, 16);
      @include font-sizing(20);
      font-weight: bold;
      line-height: 1.4;
      color: #ffffff;
    }
  }
}