@import "~theme.scss";

.root {
  @extend .container;
  display: flex;
  @include spacing(padding-top, 24);
  @include spacing(padding-bottom, 24);
  display: flex;
  justify-content: space-between;
  align-items: center;

  border-style: solid;
  border-image: linear-gradient(to right, #ffbe99, #9d50ff, #6109d1);
  border-image-slice: 1;
  border-image-width: 2px 0 0 0;

  @include font-sizing(16);
  font-weight: bold;
  opacity: 0.9;
  color: rgba(255, 255, 255, 0.64);
  letter-spacing: 0.32px;
  @media (max-width: $breakpoint-mobile)  {
    flex-direction: column;
    justify-content: center;
  }
}